body {
  color: var(--general-color);
  background-color: var(--white-color);
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
}

.container {
  width: 320px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.general-title {
  color: var(--second-color);
  font-size: 24px;
  font-weight: 800;
}

.title {
  color: var(--second-color);
  font-size: 20px;
  font-weight: 600;
  line-height: 100%;
}

.button-frame {
  color: var(--general-color);
  border: 1px solid var(--general-color);
  border-radius: 4px;
  padding: 1px 10px;
  font-family: Inter;
  font-size: 10px;
  font-weight: 600;
  line-height: 20px;
}

.button-inherit {
  color: var(--button-color);
  border: 1px solid var(--button-color);
  border-radius: 6px;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
}

.button-blue {
  color: var(--white-color);
  border: 3px solid var(--button-color);
  background: var(--button-color);
  border-radius: 8px;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
}

.button-grey {
  color: var(--general-color);
  background: rgba(95, 105, 128, .2);
  border: 1px solid rgba(95, 105, 128, .2);
  border-radius: 8px;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
}

.hidden {
  overflow: hidden;
}

@media screen and (min-width: 479.98px) {
  .container {
    width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 767.98px) {
  .container {
    width: 767px;
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media screen and (min-width: 991.98px) {
  body {
    font-size: 20px;
  }

  .container {
    width: 992px;
    padding-left: 40px;
    padding-right: 40px;
  }

  .general-title {
    font-size: 80px;
  }

  .title {
    font-size: 48px;
  }

  .button-frame {
    font-size: 12px;
  }

  .button-inherit, .button-blue {
    border: 3px solid var(--button-color);
    border-radius: 8px;
    font-size: 26px;
  }

  .button-grey {
    border: 3px solid rgba(95, 105, 128, .2);
    border-radius: 8px;
    font-size: 26px;
  }
}

@media screen and (min-width: 1439.98px) {
  body {
    font-size: 24px;
  }

  .container {
    width: 1440px;
    padding-left: 80px;
    padding-right: 80px;
  }

  .general-title {
    font-size: 100px;
  }

  .title {
    font-size: 60px;
  }
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

:focus, :active, a :focus, a :active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 14px;
  line-height: 1;
}

input, button, textarea {
  font-family: inherit;
}

input ::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button ::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a :visited, a :hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

